* {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
	outline: none;
}
$black: #000000;
$white: #FFFFFF;
$commit-green: #9BE9A8;
$success: #40C463;
$warning: #EFB008;
$error: #D83232;
@font-face {
	font-family: 'Red-Hat-Display';
	src: 
		local('fonts/red-hat-display'), url('/src/assets/fonts/red-hat-display/red-hat-display-light.ttf') format('ttf'),
		local('fonts/red-hat-display'), url('/src/assets/fonts/red-hat-display/red-hat-display-light.woff2') format('woff2');
	font-display: block;
	font-weight: 300;
}
